@import '~node_modules/modern-normalize/modern-normalize.css';

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  overflow-x: clip;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  color: $main-color;
  font-family: $main-font;
  font-size: 12px;
  font-weight: 500;
  line-height: calc(20 / 12);
  letter-spacing: 0.04em;
  background-size: contain;
  background-attachment: fixed;
  overflow-x: clip;

  font-weight: 500;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  display: block;
  color: currentColor;
  // cursor: $cursor-dark;
}

textarea {
  resize: none;
}

// üí°Folks, let's use this class to hide elements that have to be invisible
.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
  pointer-events: none;
}
